import { css } from "styled-components";

import { colors, displayFontSizes, fontFamily, spacingMap } from "../styleguide";

import { InputWrapperProps } from "./Input.styles";


export const transition = {
	duration: "0.4s",
	function: "cubic-bezier(0.215, 0.61, 0.355, 1)",
};
export const SharedWrapperStylesWithTextArea = css<InputWrapperProps>`
	position: relative;
	min-height: 40px;
	background-color: ${colors.white};

	display: flex;
	align-items: center;
	flex-wrap: wrap;
	gap: ${spacingMap.xs};
	box-sizing: border-box;

	border-radius: 8px;
	border: 1px solid ${({ validation }) => validation === "invalid" ? colors.destructive : colors.defaultGray};

	${({ disabled }) => disabled ? `
			opacity: 0.66;
			cursor: not-allowed;
			background-color: ${colors.lightestGray};
		`
		: `
			&:hover {
				border-color: ${colors.black};
			}
		`}
	&:focus-within {
		outline: none;
		box-shadow: 0 0 0 4px ${colors.midBlue}40;
		border: 1px solid ${({ validation }) => validation === "invalid" ? colors.destructive : colors.midBlue};
	}

	:has(input:-webkit-autofill) {
		//Google Chrome autofill color
		background-color: rgb(232, 240, 254);
	}
`;

export const sharedStylesWithTextArea = css`
	box-sizing: border-box;
	border-radius: 8px;

	min-width: 60px;
	flex: 1;

	font-family: ${displayFontSizes.bodyCopy.$fontFamily};
	font-size: ${displayFontSizes.bodyCopy.$fontSize};
	line-height: ${displayFontSizes.bodyCopy.lineHeight};
	font-weight: ${displayFontSizes.bodyCopy.$fontWeight};

	appearance: none;

	transition-duration: ${transition.duration};
	transition-timing-function: ${transition.function};

	color: ${colors.black};

	border: none;
	outline: none;

	&::placeholder {
		font-family: ${fontFamily.larsseit};
		color: ${colors.defaultGray};
	}

	&:disabled {
		cursor: not-allowed;
		background-color: ${colors.lightestGray};
	}

	&:-webkit-autofill {
		-webkit-box-shadow: 0 0 0 100px rgb(232, 240, 254) inset !important;
	}
`;

